/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
button,
input,
textarea {
	font-family: inherit;
}
@font-face {
  font-family: Literal;
  font-display: swap;
  font-weight: 300;
  src: 
    url(../../fonts/literal-light.woff2) format('woff2'),
    url(../../fonts/literal-light.woff) format('woff'),
    url(../../fonts/literal-light.otf) format('opentype')
  ;
}

@font-face {
  font-family: Literal;
  font-display: swap;
  font-weight: 400;
  src: 
    url(../../fonts/literal-regular.woff2) format('woff2'),
    url(../../fonts/literal-regular.woff) format('woff'),
    url(../../fonts/literal-regular.otf) format('opentype')
  ;
}

@font-face {
  font-family: Literal;
  font-display: swap;
  font-weight: 700;
  src: 
    url(../../fonts/literal-bold.woff2) format('woff2'),
    url(../../fonts/literal-bold.woff) format('woff'),
    url(../../fonts/literal-bold.otf) format('opentype')
  ;
}

@font-face {
  font-family: Karton;
  font-display: swap;
  font-weight: 400;
  src: 
    url(../../fonts/karton-regular.woff2) format('woff2'),
    url(../../fonts/karton-regular.woff) format('woff'),
    url(../../fonts/karton-regular.otf) format('opentype')
  ;
}

/* Скрыть визуально, оставить для скринридеров */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
/* Заголовок секций */
.section-title {
  font-family: Karton, Arial, sans-serif;
  font-size: 42px;
  line-height: 47px;
  text-transform: uppercase;
}

.section-title .marker {
  color: var(--color-accent-2);
}

@media (max-width: 690px) {
  .section-title {
    font-size: 36px;
    line-height: 40px;
  }
}

@media (max-width: 440px) {
  .section-title {
    font-size: 30px;
    line-height: 40px;
  }
}

/* Подзаголовок секций */
.section-subtitle {
  text-align: center;
  margin-top: 20px;
  font-size: 16px;
  line-height: 24px;
  color: var(--color-text);
}

@media (max-width: 690px) {
  .section-subtitle {
    margin-top: 10px;
    font-size: 12px;
    line-height: 20px;
  }
}

/* Синяя кнопка */
.btn-blue {
  border: none;
  background-color: var(--color-accent-1);
  border-radius: 10px;
  color: #fff;
  cursor: pointer;
  padding: 7px 16px;
  font-size: 15px;
  transition: background-color ease .2s, opacity ease .2s, transform ease .2s;
  text-decoration: none;
  line-height: 18px;
}

.btn-blue:hover {
  /* background-color: var(--color-accent-2); */
  opacity: .7;
}

.btn-blue:active {
  transform: scale(.95);
}

/* Список соцсетей */
.social-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
}

.social-list__icon {
  width: 55px;
  height: 55px;
}

.social-list__icon {
  transition: opacity ease .2s;
}

.social-list__item:hover .social-list__icon {
  opacity: .5;
}

.social-list__item:active .social-list__icon {
  opacity: .2;
}

@media (max-width: 870px) {
  .social-list__icon {
    width: 45px;
    height: 45px;
  }
}

@media (max-width: 670px) {
  .social-list__icon {
    width: 40px;
    height: 40px;
  }
}

/* Декоративные изображения */
.decor-image {
  pointer-events: none;
  user-select: none;
  z-index: -1;
}

@media (max-width: 650px) {
  .decor-image {
    display: none;
  }
}

/* Оранжевая градиентная кнопка */
.orange-gradient-btn {
  font-weight: 700;
  font-size: 18px;
  text-decoration: none;
  cursor: pointer;
  color: #fff;
  background: none;
  border: none;
}

.orange-gradient-btn__inner {
  background: linear-gradient(90deg, #faeea8, var(--color-accent-2));
  border: none;
  border-radius: 10px;
  padding: 13px 60px;
  transition: opacity ease .2s;
}

.orange-gradient-btn:hover .orange-gradient-btn__inner {
  opacity: .7;
}

.orange-gradient-btn:active .orange-gradient-btn__inner {
  opacity: .4;
  transition: opacity ease .01s;
}
