.our-works {
  
}

.our-works__row {
  padding-top: 100px;
  position: relative;
}

.our-works__title {
  text-align: center;
}

.our-works__content {
  margin-top: 35px;
  display: flex;
  flex-wrap: wrap;
  /* min-height: 600px; */
}

/* Works Decor Images */
.our-works__decor-image {
  position: absolute;
}

.our-works__decor-image_1 {
  left: 0;
}

.our-works__decor-image_2 {
  right: 0;
  bottom: 0;
}

@media (max-width: 1450px) {
  .our-works__decor-image_1 {
    transform: translate(-20%, 0);
  }

  .our-works__decor-image_2 {
    transform: translate(40%, 0);
  }
}

