  .our-works__decor-image_1 {
    transform: translate(-30%, 0);
  }
}

@media (max-width: 1210px) {
  .our-works__decor-image {
    display: none;
  }
}

/* Line of works */
.our-works__line {
  display: flex;
  flex-wrap: wrap;
}

/* Work item */
.work {
  flex-basis: 25%;
  height: 300px;
  position: relative;
}

.work__image {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Work Info */
.work__info {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, .9);
  z-index: 10;
  padding: 0 15px;
  padding-top: 50px;
  padding-bottom: 35px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  cursor: zoom-in;
  opacity: 0;
  transition: opacity ease .3s;
}

.work:hover .work__info {
  opacity: 1;
}

.work:active .work__info {
  opacity: .6;
  transition: opacity ease .1s;
}

.work:hover .work__zoom::before {
  animation: pulse infinite 1s forwards linear alternate-reverse;
}

.work__title {
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
  display: block;
}

.work__title::first-letter {
  color: var(--color-accent-2);
}

.work__bottom {
  font-weight: 300;
}

.work__description {
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 10px;
  /* cursor: text; */
}

.work__price {
  font-size: 16px;
  line-height: 20px;
  /* cursor: text; */
}

.work__zoom {
  background: none;
  border: none;
  cursor: zoom-in;
  position: relative;
  transition: opacity ease .3s;
}

.work__zoom path {
  transition: fill ease .2s;
  fill: var(--color-accent-2);
}

.work__zoom::before {
  content: "";
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, .4);
  position: absolute;
  z-index: -1;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.work__zoom:hover {
  opacity: .6;
}

@keyframes pulse {
  0% {
    transform: translate(-50%, -50%) scale(1);
  }

  100% {
    transform: translate(-50%, -50%) scale(1.7);
  }
}

@media (max-width: 1050px) {
  .work {
    flex-basis: 33.333%;
