}

.question.active .question__icon {
  transform: rotate(90deg);
}

.question__icon {
  position: absolute;
  right: 60px;
  top: 20px;
  transition: transform ease .5s;
}

.question__title {
  color: var(--color-text);
  font-size: 22px;
  line-height: 32.5px;
  display: block;
  margin-right: 70px;
}

.question__title::first-letter {
  color: var(--color-accent-2);
}

.question__answer {
  padding: 20px 30px;
  padding-top: 15px;

  padding: 0 30px;
  max-height: 0;
  will-change: max-height;
  transition: max-height ease .5s, padding ease .5s;
  box-sizing: content-box;
}

.question.active .question__answer {
  padding-top: 15px;
  padding-bottom: 20px;
}

.question__answer-text {
  font-size: 18px;
  line-height: 28.5px;
  color: var(--color-text);
}

@media (max-width: 910px) {
  .questions__row {
    padding-top: 50px;
  }
}

@media (max-width: 750px) {
  .question__title {
    font-size: 18px;
    line-height: 24px;
  }

  .question__answer-text {
    font-size: 14px;
    line-height: 24.5px;
  }
}

@media (max-width: 520px) {
  .question__content {
    margin-top: 30px;
  }

  .question__icon {
    right: 20px;
  }

  .question__title {
    margin-right: 50px;
  }
}

@media (max-width: 440px) {
  .questions__content {
    margin-top: 30px;
  }

  .question__title {
    font-size: 16px;
    line-height: 22px;
  }

  .question__answer-text {
    font-size: 12px;
    line-height: 18.5px;
  }
}

/* Секция документов и сертификатов */
.documents {
  margin-top: 100px;
  
}

.documents__row {
  background-color: var(--color-accent-3);
  padding-top: 100px;
  padding-bottom: 50px;
}

.documents__title {
  text-align: center;
}

.documents__content {
  margin-top: 55px;
}

/* Documents Slider */
.documents-slider__slide {
  display: flex;
  justify-content: center;
  transition: opacity ease .2s;
}

.documents-slider__slide:hover {
  opacity: .7;
}

.documents-slider__slide:active {
  opacity: .3;
}

.documents-slider__slide-image {
  width: 260px;
  height: 366px;
  object-fit: cover;
  cursor: zoom-in;
}

/* Documents Slider Nav Buttons */

.documents-slider__nav {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  column-gap: 30px;
}

.documents-slider__nav-btn {
  background: none;
  border: none;
  padding: 20px;
  cursor: pointer;
  transition: opacity ease .2s;
}

.documents-slider__nav-btn:hover {
  opacity: .6;
}

.documents-slider__nav-btn:active {
  opacity: .2;
}

.documents-slider__nav-btn.swiper-button-disabled {
  opacity: .2;
  cursor: default;
}

@media (max-width: 810px) {
  .documents-slider__slide-image {
    width: 200px;
    height: 290px;
  }
}

@media (max-width: 690px) {
  .documents {
    margin-top: 70px;
  }

  .documents__row {
    padding-top: 60px;
    padding-bottom: 30px;
  }

  .documents__content {
    margin-top: 35px;
  }

  .documents-slider__nav-btn-image {
    width: 120px;
  }

  .documents-slider__nav {
    column-gap: 10px;
  }
}

@media (max-width: 450px) {
  .documents-slider__slide-image {
    width: 165px;
    height: 255px;
  }
}

@media (max-width: 380px) {
  .documents-slider__slide-image {
    width: 210px;
    height: 291px;
  }

  .documents-slider__nav-btn {
    padding: 20px 10px;
  }

  .documents-slider__nav-btn-image  {
    width: 100px;
  }
}

/* === Секция контактов === */
.contacts {
  overflow: hidden;
  
}

.contacts__row {
  padding-top: 100px;
}

.contacts__title {
  text-align: center;
}

.contacts__content {
  padding-top: 60px;
}

/* Map Wrapper */
.contacts__map-wrapper {
  margin-bottom: 35px;
  position: relative;
}

.contacts__map-wrapper iframe,
.contacts__map-wrapper #map {
  position: relative;
  z-index: 100;
  height: 400px;
}

/* Map Wrapper Decor */
.contacts__map-decor-image {
  position: absolute;
  top: 50%;
  transform: translate(0, -47%);
  z-index: -1;
}

.contacts__map-decor-image_1 {
  left: -350px;
}

.contacts__map-decor-image_2 {
  right: -350px;
  transform: translate(0, -50%);
}

.contacts__info {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 20px;
}

/* Info Block */
.info-block {
  background-color: #F1F1F1;
  padding: 30px 40px;
  flex-basis: 50%;
  transition: color ease .2s, background-color ease .2s, border-radius ease .2s, box-shadow ease .2s;
}

.info-block:hover {
  border-radius: 10px;
  background-color: var(--color-accent-1);
  color: #fff;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.35);
}

.info-block:hover .blue {
  color: #cce9ff;
}

.info-block__title {
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 20px;
}

.info-block__info {
  display: flex;
  flex-direction: column;
}

.info-block__item {
  font-size: 16px;
  line-height: 28px;
}

.info-block__item.blue {
  color: #9ac2e2;
}

a.info-block__item {
  color: #9ac2e2;
  cursor: pointer;
  text-decoration: none;
  transition: color ease .2s;
}

a.info-block__item:hover {
  text-decoration: underline;
}

a.info-block__item:active {
  opacity: .5;
}

.info-block__text {
  font-size: 16px;
  line-height: 22px;
}

/* Social List */
.contacts__social {
  margin-top: 35px;
}

@media (max-width: 910px) {
  .contacts__row {
    padding-top: 50px;
  }

  .contacts__info {
    grid-column-gap: 10px;
  }
}

@media (max-width: 870px) {
  .info-block__title {
    font-size: 16px;
    margin-bottom: 15px;
  }

  .info-block__item {
    font-size: 12px;
    line-height: 20px;
  }

  .info-block__text {
    font-size: 12px;
    line-height: 16px;
  }

  .contacts__map-wrapper iframe,
  .contacts__map-wrapper #map {
    height: 350px;
  }
}

@media (max-width: 690px) {
  .contacts__content {
    padding-top: 30px;
  }
}

@media (max-width: 550px) {
  .contacts__map-wrapper iframe,
  .contacts__map-wrapper #map {
    height: 300px;
  }

  .info-block {
    padding: 30px 15px;
  }
}

@media (max-width: 530px) {
  .contacts__info {
    grid-template-columns: 1fr;
    grid-auto-rows: minmax(min-content, 1fr);
    grid-row-gap: 10px;
  }

  .contacts__map-decor-image {
    display: none;
  }

  .contacts__map-wrapper {
    margin-bottom: 20px;
  }
}

/* === Подвал сайта === */
.footer {
  margin-top: 70px;
  background-color: var(--color-accent-1);
  color: #fff;
}

.footer__row {
  padding-top: 28px;
}

.footer__line {
  display: grid;
  grid-column-gap: 105px;
  grid-template-columns: repeat(3, 1fr);
  padding-bottom: 34px;
}

@media (max-width: 990px) {
  .footer__line {
    grid-column-gap: 60px;
  }
}

@media (max-width: 850px) {
  .footer__line {
    grid-column-gap: 30px;
  }
}

@media (max-width: 800px) {
  .footer__line {
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 30px;
    justify-items: center;
  }

  .footer-form {
    grid-column: span 2;
  }
}

@media (max-width: 410px) {
  .footer__line {
    grid-template-columns: repeat(1, 1fr);
    justify-items: stretch;
  }

  .footer-form {
    grid-column: 1;
  }
}

/* Footer Block */
.footer-block {
  display: flex;
  flex-direction: column;
  flex-basis: 35%;
}

.footer-block__title {
  margin-bottom: 17px;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  color: var(--color-accent-2);
}

/* Footer Nav */
.footer-nav__list {
  display: flex;
  flex-direction: column;
}

.footer-nav__item {
  position: relative;
}

.footer-nav__item:not(:last-child) {
  margin-bottom: 15px;
}

.footer-nav__icon {
  position: absolute;
  left: -10px;
  top: 50%;
  transform: translate(-100%, -50%);
}

.footer-nav__link {
  font-weight: 300;
  font-size: 16px;
  line-height: 21.5px;
  color: inherit;
  text-decoration: none;
  transition: opacity ease .2s;
  display: flex;
  align-items: center;
  column-gap: 10px;
}

.footer-nav__link[href]:hover {
  text-decoration: underline;
  opacity: .9;
}

.footer-nav__link[href]:active {
  opacity: .4;
}

@media (max-width: 410px) {
  .footer-block__title {
    font-size: 16px;
    line-height: 16px;
    margin-bottom: 13px;
  }

  .footer-nav__link {
    font-size: 12px;
  }

  .footer-nav__item:not(:last-child) {
    margin-bottom: 7px;
  }
}

/* Footer Form Wrapper */
.footer-form {
  flex-basis: 30%;
}

.footer-form__title {
  margin-bottom: 11px;
}

/* Footer Form */
.contact-form.disabled {
  opacity: .5;
}

.wpcf7 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
}

.contact-form label {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 18px;
}

.contact-form textarea {
  grid-column: span 2;
  min-height: 75px;
  resize: vertical;
  border: none;
}

.contact-form input,
.contact-form textarea {
  border-radius: 2px;
  border: none;
  margin-top: 5px;
  padding: 5px 10px;
}

.contact-form input.error,
.contact-form textarea.error {
  outline: 1px solid coral;
}

.contact-form input[type="submit"] {
  justify-self: end;
  grid-column: 2/2;

  display: inline-block;
  background: var(--color-accent-2);
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  padding: 9px 60px;
  font-weight: 700;
  font-size: 16px;
  transition: opacity ease .2s, transform ease .1s;
}

.contact-form input[type="submit"]:not(:disabled):hover {
  opacity: .7;
}

.contact-form input[type="submit"]:not(:disabled):active {
  transform: scale(.95);
}

.contact-form input[type="submit"]:disabled {
  opacity: .5;
}

@media (max-width: 410px) {
  /* .contact-form, */
  .wpcf7 {
    grid-template-columns: 1fr;
  }

  .contact-form textarea,
  .contact-form input[type="submit"] {
    grid-column: 1;
  }

  .contact-form input[type="submit"] {
    width: 100%;
  }
}

/* Footer Bottom */
.footer__bottom {
  text-align: center;
  padding: 22px 0;
  border-top: 1px solid var(--color-accent-2);
}

/* === Стилизация попапа === */
.popup {
  position: fixed;
  top: 70px;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 9999;
  transform: translate(-50%, -999px);
  opacity: 0;
  visibility: hidden;
  transition: opacity ease .5s .25s, visibility ease .5s .25s, transform ease .5s;
}

.popup.active {
  transform: translate(-50%, 0);
  opacity: 1;
  visibility: visible;
}

.popup__content {
  padding: 40px 20px 30px 20px;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 8px 31px -9px rgba(0, 0, 0, 0.25);
  position: relative;
}

.popup-close {
  position: absolute;
  background: none;
  border: none;
  right: 10px;
  top: 10px;
  font-size: 18px;
  cursor: pointer;
  padding: 10px;
}

.popup-close__icon path {
  transition: fill ease .2s, opacity ease .2s;
}

.popup-close:hover .popup-close__icon path {
  fill: var(--color-accent-2);
  opacity: .9;
}

.popup-close:active .popup-close__icon path {
  opacity: .3;
  transition: opacity ease .1s;
}

.popup__title {
  font-size: 24px;
  margin-bottom: 10px;
}

.popup__descr {
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 15px;
  max-width: 500px;
}

.popup__accept {
  align-self: flex-end;
}

.popup__accept .popup__accept-inner {
  padding: 10px 40px;
}

@media (max-width: 770px) {
  .popup {
    width: 450px;
  }

  .popup__title {
    font-size: 20px;
  }

  .popup__descr {
    font-size: 16px;
    line-height: 22px;
  }
  
  .popup__accept {
    font-size: 16px;
  }

  .popup__accept .popup__accept-inner {
    padding: 9px 40px;
  }
}

@media (max-width: 520px) {
  .popup {
    width: 360px;
  }

  .popup__title {
    font-size: 16px;
  }

  .popup__descr {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 20px;
  }

  .popup__accept {
    font-size: 14px;
  }
}

@media (max-width: 400px) {
  .popup {
    width: 100%;
    padding: 0 10px;
  }
}

